import { useAuth } from "../sharedVars";
import Input from '../Input/Input';


export const RegForm = () => {
    const {
        login, setLogin,
        pass, setPass,
        reg, setReg,
        fam, setFam,
        name, setName,
        midname, setMidname,
        otd, setOtd,
        email, setEmail } = useAuth();

    const createUser = () => {
        fetch('index.php', {  //    index.php    
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                login: login,
                pass: window.md5(pass),
                fam: fam,
                name: name,
                midname: midname,
                otd: otd,
                email: email,
                reg: reg,
            })
        })
            .then(response => {
                if (!response.ok) {
                    throw new Error(' ');
                }
                return response.json();
            })
            .then(data => {
                console.log(' :', data);
                if (data === "-1") setReg("-1");
                //setReg(false);
            })
            .catch(error => {
                console.error(':', error);
            });

    }

    return (
        <>
            <form id="regForm" method='POST' onSubmit={(e) => {
                e.preventDefault();
                createUser();
                setReg('login');
            }}>
                <div className="name"></div>
                <Input label=''
                    id="log"
                    value={login}
                    action={(e) => setLogin(e.target.value)}
                    required />
                <Input label='' type="password"
                    id="pass"
                    value={pass}
                    action={(e) => setPass(e.target.value)}
                    required />
                <Input label=''
                    id="fam"
                    value={fam}
                    action={(e) => setFam(e.target.value)}
                    required />
                <Input label=''
                    id="name"
                    value={name}
                    action={(e) => setName(e.target.value)}
                    required />
                <Input label=''
                    id="midname"
                    value={midname}
                    action={(e) => setMidname(e.target.value)}
                    required />
                <Input label=''
                    id="otd"
                    value={otd}
                    action={(e) => setOtd(e.target.value)}
                    required />
                <Input label=''
                    id="email"
                    value={email}
                    type='email'
                    action={(e) => setEmail(e.target.value)}
                    required />
                <div>
                    <button id="returnButton" onClick={() => setReg('login')}></button>
                    <button id="enterButton"></button>
                </div>
            </form>
        </>
    )
};