import { useAuth } from "../sharedVars";
import Input from '../Input/Input';


export const LoginForm = () => {
    const {
        login, setLogin,
        pass, setPass,
        reg, setReg,
        fam, setFam,
        name, setName,
        midname, setMidname,
        otd, setOtd,
        email, setEmail} = useAuth();

        const userLogin = () => {
            fetch('index.php', {  //    index.php    
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    login: login,
                    pass: window.md5(pass),
                    reg: reg,
                })
            })
                .then(response => {
                    if (!response.ok) {
                        throw new Error(' ');
                    }
                    return response.json();
                })
                .then(data => {
                    console.log(' :', data);
                    if (data === "-2") setReg("-2");
                    if (data === "login") window.location.reload();
                })
                .catch(error => {
                    console.error(':', error);
                });
    
        }

    return (
        <>
            <form id="loginForm" method='POST' onSubmit={(e) => { 
                e.preventDefault();
                userLogin();
                }}>
                <div className="name"></div>
                <Input label=''
                    id="log"
                    value={login}
                    action={(e) => setLogin(e.target.value)} />
                <Input label='' type="password"
                    id="pass"
                    value={pass}
                    action={(e) => setPass(e.target.value)} />
                <div>
                    <button type="submit" id="enterButton"></button>
                    <button id="newUser" onClick={
                        (e) => {
                            e.preventDefault();
                            setReg('newReg');
                        }}></button>
                </div>
            </form>
        </>
)}