import './App.css';
import React, { useState } from 'react';
import { LoginWindow } from './components//login/loginWindow';
import { SharedProvider } from './components/sharedVars';
import AccountMenu from './components/AccountMenu';
import { HideBlock } from './components/login/hideblock';


function App() {

  const [showMenu, setshowMenu] = useState('');
  let startMenu = document.getElementById("menu_box");

  return (
    <SharedProvider>
      {startMenu === null && <LoginWindow />}
      {startMenu && <HideBlock />}
      {startMenu && <AccountMenu />}
    </SharedProvider>

  );
}

export default App;
